
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #000000;
  margin-top: 60px;
}
.title a {
  color:#000000;
  text-decoration: none;
}
.header-nav ul {
  list-style: none;
  padding:0;
}
.header-nav ul li {
  display: inline-block;
  margin: 0 0.7em;
  padding: 0 0.5em;
}
.header-nav ul li a {
  color:#000000;
  display: block;
  text-decoration: none;
}
.underline {
  border-width: 0 0 2px;
  border-style: solid;
}
ul {
  list-style-position: inside;
}

































